@font-face {
    font-family: 'Maison Neue Medium';
    src: url('../fonts/MaisonNeueWEB-Medium.woff2') format('woff2'),
    url('../fonts/MaisonNeueWEB-Medium.woff') format('woff');
}

@font-face {
    font-family: 'Noe Display';
    src: url('../fonts/NoeDisplayMedium.eot');
    src: url('../fonts/NoeDisplayMedium.woff') format('woff');
}


// visual grid
$visual-grid: true;
$visual-grid-color: yellow;
$visual-grid-index: front;
$visual-grid-opacity: 0.2;

// base
@import
  "node_modules/normalize.scss/normalize",
  "bourbon",
  "base/base", // includes grid-settings, which must be imported before neat
  "neat";

// modules
@import
  "modules/fonts",
  "modules/vars",
  "modules/common",
  "modules/visual-grid",
  "modules/footer",
  "modules/header",
  // "modules/simple-hero", // basic hero image
  "modules/dynamic-hero", // dynamically-sized
  // "modules/text-section", // just text
  // "modules/image-section", // full-width image
  // "modules/combo-section", // image on one side, text on another
  "modules/flickity",
  "modules/home",
  "modules/utilities";

  body:before {
    display: none;
  }

  body.grid:before {
    display: block;
  }


